import { LeftOutlined, RightOutlined } from '@ant-design/icons';
import { Card, Carousel } from 'antd'
import Meta from 'antd/es/card/Meta';
import React from 'react'


const Carasoul = ({ width = "", height = "", content = [], isContainCards = false }) => {
    const contentStyle = {
        height: height,
        width: width,
        color: '#fff',
        lineHeight: '160px',
        textAlign: 'center',
        background: '#364d79',
        margin: "0"
    };
    return (
        <Carousel autoplay arrows nextArrow={<RightOutlined />} prevArrow={<LeftOutlined />} >
            {!isContainCards && content.map((item, i) => (
                <div key={i} >
                    <img src={item.img} alt="img" style={contentStyle} />
                </div>
            ))}
            {isContainCards && content.map((item, i) => (
                <div key={i}>
                    <Card
                        hoverable
                        style={{
                            width: 200,
                        }}
                        cover={<img alt="example" src="https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png" />}
                    >
                        <Meta title="Europe Street beat" description="www.instagram.com" />
                    </Card>
                </div>
            ))}
        </Carousel>
    )
}

export default Carasoul