import { Button } from 'antd';
import './App.css';
import { Routes, Route } from 'react-router-dom';
import Home from './Pages/Home';
import Header from './Components/Header';
import CategoryNav from './Components/CategoryNav';

function App() {
	return (
		<>
			<Header />
			<CategoryNav />
			<Routes>
				<Route path='/' element={<Home />} />
			</Routes>
		</>
	);
}

export default App;
